@media only screen and (max-width: 768px) {
  .faBars {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 75%);
    font-size: 1.8rem;
    cursor: pointer;
    z-index: 10;
  }

  .navBlock {
    display: none;
  }

  #navText {
    display: none;
  }

  label:before {
    display: none;
  }
  .searchResults {
    display: none;
  }
}

