.popOutlinkActive {
  color: rgb(121, 64, 255);
}

.popOutNavlink {
  color: rgb(255, 255, 255);
  display: block;
  font-size: 20px;
  height: 50px;
  text-align: center;
  align-items: center;
  text-decoration: none;
  margin-top: 20px;
  width: 100%;
