.popOutNav {
  padding-top: 80px;
  position: fixed;
  top: 0px;
  right: 0px;
  height: 100%;
  width: 50%;
  background-color: rgb(54, 54, 54);
  z-index: 1;
}

.side-enter {
  right: -500px;
}

.side-enter.side-enter-active {
  right: 0px;
  transition: right 300ms ease-in;
}

.side-leave {
  right: 0px;
}

.side-leave.side-leave-active {
  right: -500px;
  transition: right 300ms ease-in;
}

.fixed {
  position: fixed;
  width: 100%;
  top: 0px;
  right: 0px;
  z-index: 6969;
}



